<template>
  <svg xmlns="http://www.w3.org/2000/svg"
       :width="width"
       :height="height"
       :viewBox="`0 0 40 40`"
       :aria-labelledby="iconName"
       role="presentation"
  >
    <title>{{ iconName }}</title>
    <g :fill="fillColor" :stroke="strokeColor">
      <slot />
    </g>
  </svg>
</template>

<script>
export default {
  props: {
    iconName: {
      type: String,
      default: 'box'
    },
    width: {
      type: [Number, String],
      default: 40
    },
    height: {
      type: [Number, String],
      default: 40
    },
    fillColor: {
      type: String,
      default: 'currentColor'
    },
    strokeColor: {
      type: String,
      default: 'currentColor'
    }
  }
}
</script>

<style scoped>

</style>
